.pc-speaker-photo {
  position: relative; }
  .pc-speaker-photo img {
    filter: grayscale(1) contrast(1.2);
    mix-blend-mode: difference; }
  @supports (mix-blend-mode: difference) {
    .pc-speaker-photo::before, .pc-speaker-photo::after {
      content: "";
      display: block;
      width: 230%;
      height: 47%;
      position: absolute;
      bottom: 30%;
      left: 0; }
    .pc-speaker-photo-right::before, .pc-speaker-photo-right::after {
      left: auto;
      right: 0; } }
  .pc-speaker-photo::before {
    mix-blend-mode: darken;
    background-color: #212529; }
  .pc-speaker-photo::after {
    mix-blend-mode: color; }
  .pc-speaker-photo-yellow::after {
    background-image: linear-gradient(-30deg, #c848d9, #ffff0e); }
  .pc-speaker-photo-blue::after {
    background-image: linear-gradient(30deg, #e80e8b, #035fcc); }
  .pc-speaker-photo-green::after {
    background-image: linear-gradient(-30deg, #ffff0e, #a4ff00); }
  .pc-speaker-photo-orchid::after {
    background-image: linear-gradient(-30deg, #c848d9, #00beff); }
  .pc-speaker-photo-cyan::after {
    background-image: linear-gradient(-30deg, #ffff0e, #36ffff); }
